*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
}

ul {
  list-style: none;
}

button,
input,
textarea {
  border: none;
  font-size: 1em;
}

textarea {
  font-family: inherit;
}
/* End of Reset */

html {
  --black: #000;
  --dark-gray: #181818;
  --gray: #383838;
  --light-gray: #555;
  --white: #ccc;

  --beige: #ca8;
}
